//"new"„Çí‰Ωø„Çè„Å™„ÅÑÂ†¥Âêà„ÄÅ'this'„Åå„Ç∞„É≠„Éº„Éê„É´„ÄÅwindow„Å´„Å§„Å™„ÅêÂïèÈ°å„Åå„ÅÇ„Çã„ÄÅ
function CartV1(){
    this.cart = [];
    this.currentId = 0;
}

CartV1.prototype.getNewId = function(){
    this.currentId++;
    return this.currentId;
};

CartV1.createItem = function(name,price){
    return{
        name,
        price
    };
};

CartV1.prototype.addItem = function(item){
    this.cart.push({
        ...item,
        id: this.getNewId(),
    });
};

CartV1.prototype.clearCart = function(item){
    this.cart = [];
    this.currentId = 0;
}
//new„Çí‰Ωø„Å£„Å¶Á©∫„ÅÑ„Å¶„Çã„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÇíÁîüÊàê
const cartV1 = new CartV1();
cartV1.addItem(CartV1.createItem("„Çπ„Ç§„Ç´",1900));
cartV1.addItem(CartV1.createItem("„É™„É≥„Ç¥",200));
cartV1.addItem(CartV1.createItem("„Éê„Éä„Éä",100));

//Clsss„Çí‰Ωø„Å£„Å¶ÊñπÊ≥ï
class CartV2{
    cart;
    currentId;
    constructor(){
        this.cart = [];
        this.currentId = 0;
    }
    getNewId =()=>{
        this.currentId++;
        return this.currentId;
    };
    
    createItem = (name, price) =>{
        return {
            name,
            price
        }
    }
    
    addItem = (item)=>{
            this.cart.push({
            ...item,
            id: this.getNewId(),
        })
        };
    
    clearCart = (item) => {
        this.cart = [];
        this.currentId = 0;
    }
}
//‰∫àÁ¥ÑË™û"NEW"„Åß„Ç§„É≥„Çπ„Çø„É≥„ÇπÁîüÊàê
const cartV2 = new CartV2();
cartV2.addItem(cartV2.createItem("„Çπ„Ç§„Ç´",1900));
cartV2.addItem(cartV2.createItem("„É™„É≥„Ç¥",200));
cartV2.addItem(cartV2.createItem("„Éê„Éä„Éä",100));
